@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
	html {
		scroll-behavior: smooth; /* Does NOT work on Safari */
	}

	body {
		font-family: Rubik, sans-serif;
	}

	/*
**  Set Text Selection Style
*/
	::selection {
		@apply text-white;
		@apply bg-green-400;
	}
}

@layer utilities {
}

/* page Layout Styles */
body {
	grid-template-columns: auto 1fr;
	grid-template-rows: auto 1fr auto;
}

aside {
	grid-row: span 2;
}

footer {
	grid-column: 1 / -1;
}

/* .sec--countries {
	grid-column: 0.8fr;
}
.sec--channels {
	grid-column: 1.2fr;
} */

::-webkit-scrollbar {
	width: 0.7rem; /* Mostly for vertical scrollbars */
	height: 0.5rem; /* Mostly for horizontal scrollbars*/
}

::-webkit-scrollbar-track {
	/* Foreground */
	background-color: map-get($color-grey, l4);
	@apply bg-indigo-300;
	@apply shadow-inner;
}

::-webkit-scrollbar-thumb {
	/* Background */
	margin: 0 map-get($space, s4);
	@apply bg-indigo-600;
	@apply rounded;
	@apply transition;
	@apply duration-300;
}

::-webkit-scrollbar-thumb:hover {
	@apply bg-indigo-800;
}

